.type--white {
  color: #fff;
}

.type--light {
  font-weight: 300;
}

.type--gold {
  color: #fdbf56;
}

.hyphens {
  hyphens: auto;
  overflow-wrap: break-word;
  word-wrap: break-word;
}

.object--center {
  display: auto;
  margin: auto;
}

.bg__color--light-gray {
  background-color: #FAFAFA;
}

.padding-left {
  padding-left: 0px;
}

.padding-top {
  padding-top: 15px;
}

.main--helper {
  min-height: calc(100vh - 130px);
}

.main--panel {
  min-height: 420px;
}

@media (min-width: 770px) and (max-width: 1024px) {
  .main--panel {
    min-height: 510px;
  }
}

@media (min-width: 760px) and (max-width: 768px) {
  .main--panel {
    min-height: 706px;
  }
}

.clear--left {
  clear: left;
}

.clear-right {
  clear: right;
}

.margin-top {
  margin-top: 7px;
}

.margin-top-20 {
  margin-top: 20px;
}

.box__color {
  width: 50px;
  height: 50px;
}

.red {
  background-color: red;
}

.blue {
  background-color: blue;
}

.blue__outline {
  border: 1px solid blue;
}

.yellow {
  background-color: yellow;
}

